import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { auth } from "../config/firebase";
import { actionTypes } from "../config/reducer";
import { useStateValue } from "../config/StateProvider";
import "./Login.css";

function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [{}, dispatch] = useStateValue();
  const history = useHistory();

  const createUser = (e) => {
    e.preventDefault();

    auth
      .createUserWithEmailAndPassword(email, password)
      .then((result) => {
        alert("Registration Successfull", result.email);
        console.log(result.email);
      })
      .catch((error) => {
        console.log(error.message);
      });
    setEmail("");
    setPassword("");
  };

  const login = (e) => {
    e.preventDefault();

    auth
      .signInWithEmailAndPassword(email, password)
      .then((result) => {
        alert(result.email, " Has successfully logged-In");
        dispatch({
          type: actionTypes.SET_USER,
          user: result.user,
        });
        console.log(result.user.email);
      })
      .catch((error) => {
        console.log(error.message);
      });
    setEmail("");
    setPassword("");
    history.push("/");
  };

  return (
    <div className="login">
      <div className="login__container">
        <img
          className="login__logo"
          src="https://cdn1.iconfinder.com/data/icons/logos-brands-in-colors/231/among-us-player-white-512.png"
          alt=""
        />

        <div className="login__containerDetail">
          <form onSubmit={login}>
            <h4>E-mail</h4>
            <input
              type="text"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <h4>Password</h4>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <button type="submit">Login</button>
          </form>

          <button className='registerButton' onClick={createUser}>Register Now</button>
        </div>
      </div>
    </div>
  );
}

export default Login;
