import {
  Button,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Modal,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import React from "react";
import { useState } from "react";
import db from "./config/firebase";
import "./Todo.css";
import firebase from "firebase";

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}
const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function Todos({ todo, timestamp, edited, newTimestamp }) {
  const [modalStyle] = React.useState(getModalStyle);
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [input, setInput] = useState("");

  const updateTodo = () => {
    db.collection("todos").doc(todo.id).set(
      {
        todo: input,
        edited: true,
        newTimestamp: firebase.firestore.FieldValue.serverTimestamp(),
      },
      { merge: true }
    );
    // setEdited(true);
    setOpen(false);
  };

  return (
    <>
      <Modal open={open} onClose={(e) => setOpen(false)}>
        <div style={modalStyle} className={classes.paper}>
          <h1>Hey I am a Modal</h1>
          <input
            placeholder={todo.todo}
            value={input}
            onChange={(e) => setInput(e.target.value)}
          />
          <Button onClick={updateTodo}>Update Todo</Button>
        </div>
      </Modal>
      <div className="todo__list">
        <div className="todo__values">
          <h2>{todo.todo}</h2>
          <p>{new Date(timestamp?.toDate()).toLocaleTimeString()}</p>
        </div>
        {edited ? (
          <div className="edited__box">
            <small className="edited__true">Edited</small>
            <span className="edited__timestamp">
              {new Date(newTimestamp?.toDate()).toLocaleTimeString()}
            </span>
          </div>
        ) : (
          ""
        )}
        <Button onClick={(e) => db.collection("todos").doc(todo.id).delete()}>
          Delete Me
        </Button>
        <Button onClick={(e) => setOpen(true)}>Edit</Button>
      </div>
    </>
  );
}

export default Todos;
